<template>
  <div>
    <mt-header title="预约信息" color="rgb(0,204,153)">
    	<router-link to="/resultlist" slot="left">
    		<mt-button icon="back">返回</mt-button>
    	</router-link>
    </mt-header>
    <ul>
      <li class="liItem"><div>姓名：</div><span>{{information.Name}}</span></li>
      <li class="liItem"><div>性别：</div><span>{{information.Sex}}</span></li>
      <li class="liItem"><div>身份证号：</div><span>{{information.IDCardNo}}</span></li>
      <li class="liItem"><div>预约单位：</div><span>{{information.HospitalName}}</span></li>
      <li class="liItem"><div>单位地址：</div><span>{{information.HospitalAddress}}</span></li>
      <li class="liItem"><div>预约日期：</div><span>{{information.Date}}</span></li>
    </ul>
    <div class="botBut"><mt-button size="large" @click="informationFun">取消预约</mt-button></div>
  </div>
</template>

<script>
  const axios = require('axios')
  import { Indicator,Toast } from 'mint-ui'
  export default {
    data(){
      return{
        information: ''
      }
    },
    methods:{
      informationFun(){
        Indicator.open('加载中...')
        axios({
          method: 'post',
          url: 'https://www.photo2030.com/webapi/api/Reserve/CancelReserve',
          data: {
            ReserveNo: this.information.ReserveNo,
            Token: 'B68DE9D1F8584D7099BCE47144D0351C'
          }
        }).then(res => {
          Indicator.close()
          Toast({
            message: res.data.Message,
            position: 'middle',
            duration: 5000
          })

        }).catch(error => {
          Indicator.close()
          Toast({
            message: '服务器错误',
            position: 'middle',
            duration: 3000
          })
        })
      }
    },
    created(){
      this.information = this.$route.query.Item
    }
  }
</script>

<style scoped>
  .mint-header{
  	background: rgb(0,204,153);
  }
  ul{
    width: 90%;
    margin: 0rem auto;
    box-sizing: border-box;
    font-size: 0.4rem;
    border-radius: 0.3rem;
    padding: 0.7rem 0.3rem 0 0.3rem;
  }
  .liItem{
    width: 100%;
    margin: 0.3rem 0;
  }
  .liItem>div{
    width: 2rem;
    height: auto;

  }
  .liItem>span{
    padding-left: 0.3rem;
    color: #000000;
    font-weight: 600;
  }
  .botBut{
    width: 100%;
    padding: 0 0.2rem;
    box-sizing:border-box;
  }
</style>
